"use client";

import React, { useEffect, useState } from "react";
import Image from "next/image";

const IMAGE_BASE = process.env.NEXT_PUBLIC_TMDB_IMAGE_BASE;
const API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY;
type TVShow = {
  id: number;
  name: string;
  poster_path: string;
};

const TVshows = () => {
  const [tvshows, setTvshows] = useState<TVShow[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchTVShows = async () => {
      try {
        const res = await fetch(
          `https://api.themoviedb.org/3/tv/popular?api_key=${API_KEY}&language=en-US&page=1`
        );
        const data = await res.json();
        setTvshows(data.results || []);
      } catch (error) {
        console.error("Failed to fetch TV shows", error);
      } finally {
        setLoading(false);
      }
    };

    fetchTVShows();
  }, []);

  if (loading) return <p>Loading TV Shows...</p>;

  return (

    <>
    <div className="px-6 sm:px-10 md:px-20 mb-2">
              <h1 className="text-2xl font-bold text-white flex items-center gap-2">
                {name} <FaAngleRight />
              </h1>
    </div>
    <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-6 gap-4">
      {tvshows.map((tvshow) => {
        const imagePath = tvshow.poster_path
          ? `${IMAGE_BASE}${tvshow.poster_path}`
          : "/placeholder.jpg";

        return (
          <div key={tvshow.id} className="hover:scale-105 transition">
            <Image
              src={imagePath}
              alt={tvshow.name}
              width={200}
              height={300}
              className="rounded-lg"
            />
           
          </div>

        );
      })}
    </div>
    </>
  );
};

export default TVshows;
